<template>
    <div id="fastEyeScanGame" v-if="gameSettlement">
        <van-nav-bar left-arrow title="快眼扫描 答题记录" @click-left="onClickNavLeft">
        </van-nav-bar>
        <div class="content">
            <div class="result-statistics">
				<div class="right-percent">正确率{{(result.rightCount/result.total*100).toFixed(0)}}%</div>
				<div class="statistics">
					<div class="item">
						<span class="title">答题数量</span>
						<span class="value">{{result.total}}</span>
					</div>
					<div class="item">
						<span class="title">正确数量</span>
						<span class="value">{{result.rightCount}}</span>
					</div>
					<div class="item">
						<span class="title">答题用时</span>
						<span class="value">{{result.useTime}}s</span>
					</div>
					<div class="item">
						<span class="title">剩余时间</span>
						<span class="value">{{result.remainingTime}}s</span>
					</div>
				</div>
			</div>
			<div class="result-detail">
				<div class="title">答案对比</div>
				<div class="detail">
					<div class="item" v-for="(data, idx) in dataSource" :key="idx">
						<div>{{idx + 1}}、{{data.rights}}</div>
						<div>你的答案：<span>{{data.yours}}</span></div>
						<div>正确答案：<span>{{data.rights}}</span></div>
					</div>
				</div>
				<van-row>
					<van-button @click="gameRun">再来一局</van-button>
					<van-button @click="onClickNavLeft">返回列表</van-button>
				</van-row>
			</div>
        </div>
    </div>
	<div id="fastEyeScanGame" v-else>
	    <van-nav-bar left-arrow title="快眼扫描" @click-left="onClickNavLeft">
	    </van-nav-bar>
	    <van-nav-bar
	        :left-text="`第${dataSource.length}题`"
	        right-text="提交"
	        @click-right="onClickNavRight"
	    >
	        <template #title>
	            <van-row>
	                <van-icon name="clock-o" size="18" />
	                <!-- <van-count-down v-if="countDown > 0" :time="countDown" format="HH:mm:ss" @finish="onCountDownFinish" @change="countDownChanged"/> -->
	                <div>
	                    {{ totalCountDown }}
	                </div>
	            </van-row>
	        </template>
	    </van-nav-bar>
	    <div class="content">
	        <van-button
	            v-if="!gameStarting"
	            type="primary"
	            @click="gameRun"
	            >开始答题</van-button
	        >
	        <div class="game-body" v-else>
				<div v-if="isAnswering">{{showAnswerCountDown}}</div>
	            <div v-if="!isAnswering" class="quick-look">{{answerInCurrent}}</div>
	            <div class="answer-list" v-else-if="dataSource.length > 0" v-for="(item, index) in currentQuestion" :key="index">
					<div class="answer-item" v-if="currentSelectIndex === index" :class="answerInCurrent === item?'answer-item-right':'answer-item-wrong'" @click="selectAnswer(item, index)">{{item}}</div>
					<div class="answer-item" v-else @click="selectAnswer(item, index)">{{item}}</div>
	            </div>
	        </div>
	    </div>
	</div>
</template>

<script>
import { randZF, pushHZ, levels } from "../utils/index";
import moment from "moment";
import 'moment-duration-format';
export default {
    data() {
        return {
            dataSource: [],
            countDown: 600, // 默认答题时间10分钟
			time: 600,
            isAnswering: false,
            gameStarting: false,
			gameSettlement: false,	// 游戏结算状态
            useTime: 0,
            timer: null,
			questionTimer: null,
			answerTimer: null,
			answerCountDown: 5,			// 设置默认答题时间为5s
			showHZ: 7,//每7道题添加一个汉字
			flaseQuestion: 0,//错误答题，需要存入cookie
			flaseMax: 10,//错题数量
			items: 5,//生成5个错误选项，外加一个正确选项，共六个选项
			itemLen: 5,//生成每一条选项的长度
			itemAdd: 2,//每隔itemAdd道题itemLen++
			nandu: 0,//难度系数，越小越难,有几个不同字符（可变项）
			nanduLen: 7,//每nanduLen题nandu++
			breakRow: 20,//字符长度达到breakRow开始换行
			currentSelectIndex: -1,
			
			result: {}
        };
    },
    computed: {
        // 当前题目该显示的答案
        answerInCurrent() {
			if (this.dataSource.length > 0) {
				return this.dataSource[this.dataSource.length - 1].rights;
			} else {
				return "";
			}
        },
		currentQuestion() {
			if (this.dataSource.length > 0) {
				return this.dataSource[this.dataSource.length - 1].question;
			} else {
				return [];
			}
		},
        totalCountDown() {
            return moment.duration(this.countDown, 'seconds').format("HH:mm:ss");
        },
		showAnswerCountDown() {
			return moment.duration(this.answerCountDown, 'seconds').format("HH:mm:ss");
		}
    },
    mounted() {
        // this.initGame();
    },
    methods: {
		onClickNavLeft() {
			this.destroyGameInfo();
			this.$router.back();
		},
		onClickNavRight() {
			this.gameOver();
		},
        initGame() {
            // 传进来的快眼扫描的时间
            let { flashTime } = this.$route.params;
            this.flashTime = parseInt(flashTime);
			
			if (!this.flashTime || this.flashTime == 0) {
				this.flashTime = 800;
			}
			this.countDown = this.time;
			this.gameSettlement = false;
			this.isAnswering = false;
			this.useTime = 0;
            let that = this;
            this.timer = setInterval(() => {
                if (that.countDown > 0) {
                    that.countDown--;
                } else {
                    that.gameOver();
                }
            }, 1000);
        },
		initBuild() {//处理数据
			if(this.flaseQuestion < this.flaseMax){
				var right=this.buildQuestion()
				var select=this.buildSelect(right)
				var leng=select.length+1;
				var arr=[]
				for(var i=0;i<leng;i++){
					arr.push(i)
				}
				var randIndex = Math.round(Math.random() * (arr.length - 1));//随机取一个下标
				select.splice(arr[randIndex], 0,right); //随机插入
				var thisRow = {
					rights: right,
					question: select,
					yours: '',
					isRight: false,
				};
				this.dataSource.push(thisRow);
			} else {
				// 当错题达到falseMax就结束游戏
				this.gameOver();
			}
		},
		buildQuestion() {//生成题目
			var x="";
			var _n=Math.floor((this.dataSource.length)/this.showHZ)
			var times=_n+1;
			for(var i=0;i<this.itemLen-times;i++){
				x+=randZF()
			}
			for(var i=0;i<times;i++){
				x=pushHZ(x)
			}
			return x;
		},
		buildSelect(data){//生成选项
			// console.log(data,total,level)
			var len=data.length;
			var select=[];//生成的选项
			var index_arr={};//记录随机选中的下标和替换的字母
			var checkIndex=[];//存储已选中下标，进行排除
			for(let k=0; k<len ; k++){
				var code=data.charAt(k)
				var reg=/^[\u4e00-\u9fa5]+$/;
				if(!reg.test(code)){//如果不是汉字
					checkIndex.push(k)   
				}
			}
			checkIndex.forEach((item, index) => {
				index_arr[item]=[];
			});
			for(let i=0; i<this.items; i++){
				levels(data,this.nandu,checkIndex,data,select,index_arr)
			}
			console.log(select)
			return select;
		},
		gameRun() {
			this.initGame();
			this.gameStarting = true;
			// 展示题目
			this.questionStep();
			
		},
		
		
		
        questionStep() {
			console.log("start game ---------")
			this.isAnswering = false;
			let that = this;
			if (this.questionTimer) {
				clearTimeout(this.questionTimer);
				this.questionTimer = null;
			}
			this.initBuild();
			this.questionTimer = setTimeout(() => {
				that.answerStep();
				clearTimeout(that.questionTimer);
				that.questionTimer = null;
			}, this.flashTime);
			
        },
		answerStep() {
			console.log("start answer ---------")
			this.isAnswering = true;
			let that = this;
			if (this.answerTimer) {
				clearInterval(this.answerTimer);
				this.answerTimer = null;
			}
			this.answerTimer = setInterval(() => {
				if (that.answerCountDown > 0) {
					that.answerCountDown --;
				} else {
					clearInterval(that.answerTimer);
					that.answerTimer = null;
					that.answerCountDown = 5;
					that.selectAnswer('未选择', -1);
				}
			}, 1000);
		},
		selectAnswer(answer, index) {
			clearInterval(this.answerTimer);
			this.answerTimer = null;
			this.answerCountDown = 5;
			
			this.dataSource[this.dataSource.length - 1].yours = answer;
			this.currentSelectIndex = index;
			if (this.dataSource[this.dataSource.length - 1].rights === answer) {
				this.dataSource[this.dataSource.length - 1].isRight = true;
			} else {
				this.flaseQuestion ++;
				this.dataSource[this.dataSource.length - 1].isRight = false;
			}
			if (this.flaseQuestion < this.flaseMax) {
				if(this.dataSource.length % this.itemAdd == 0){
					this.itemLen ++;
				}
				if(this.dataSource.length % this.nanduLen == 0 && this.itemLen <= this.breakRow) {
					this.nandu ++;
				}
				let that = this;
				let checkTimer = setTimeout(() => {
					that.questionStep();
					that.currentSelectIndex = -1;
					clearTimeout(checkTimer);
				}, 500);
			} else {
				// 游戏结束
				this.gameOver();
			}
		},
        gameOver() {
            this.destroyGameInfo();
            let useTime = this.time - this.countDown; // 总共用时
			let result = {};
			result.total = this.dataSource.length;
			result.useTime = useTime;
			result.remainingTime = this.countDown;
			result.rightCount = 0;
			this.dataSource.forEach((item, index) => {
				if (item.isRight) {
					result.rightCount ++;
				}
			});
			this.result = result;
			this.gameSettlement = true;
        },
        gameAgain() {
            this.initGame();
        },
        destroyGameInfo() {
            if (this.timer) {
                clearInterval(this.timer);
                this.timer = null;
            }
			if (this.questionTimer) {
				clearTimeout(this.questionTimer);
				this.questionTimer = null;
			}
			if (this.answerTimer) {
				clearInterval(this.answerTimer);
				this.answerTimer = null;
			}
        }
    },
};
</script>

<style scoped lang="less">
